<template>
  <div class="archiveList">
  <div class="mainpage_header">
    <div class="header_border">
      <span class="header_logo">
        <!-- <img src="@/assets/images/logo.png"/> -->
        <img src="@/assets/images/jylogo1.png"/>
      </span>
      <div class="header_title">
        <!-- <img src="@/assets/images/logo01.png"/> -->
      </div>
      <div class="header_icon">
        <span style="cursor: pointer;"><i class="iconfont icon-xiaoxi message"></i></span>
        <span style="margin-left: 30px; cursor: pointer;"><i class="iconfont icon-yonghu person"></i></span>
        <span style="margin-left: 30px; cursor: pointer;" class="exitIcon"><i class="iconfont icon-fork exit"></i></span>
      </div>
    </div>
  </div>
    <section class="pc-banner">
      <div class="swiper-container">
        <div class="swiper-wrapper main_content">
          <div class="swiper-slide swiper_next" v-for="item in list" :key="item.CLASSID">
            <img class="swiper_img" src="@/assets/images/noboxnovolume.jpg" v-if="item.CLASSTYPE==0"/>
            <img class="swiper_img" src="@/assets/images/box.jpg" v-if="item.CLASSTYPE==1"/>
            <img class="swiper_img" src="@/assets/images/volume.jpg" v-if="item.CLASSTYPE==2"/>
            <div class="layer-mask"></div>
            <h6 :class="item.DESCRIPTION.length>6? classB:classA">{{item.DESCRIPTION}}</h6>
            <p class="swiper_text" v-if="item.CLASSTYPE==0">无盒无卷</p>
            <p class="swiper_text" v-if="item.CLASSTYPE==1">装盒</p>
            <p class="swiper_text" v-if="item.CLASSTYPE==2">订卷</p>
            <div class="shade"></div>
            <div class="swiper_enter" @click="getTablelist(item)">
              <img src="@/assets/images/next.png"/>
            </div>
          </div>
        </div>
        <div class="swiper-pagination" style="margin-top: 6%;"></div>
      </div>
    </section>
    <!--底部-->
    <div class="mainpage_footer">
      <p>Copyright&nbsp;(c)&nbsp;2018&nbsp;天津市天深科技股份有限公司&nbsp;all&nbsp;right&nbsp;reserved</p>
