<script>
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'
import 'swiper/dist/js/swiper.min.js'
export default {
  data () {
    return {
      msg: 'aaa',
      list: [],
      classA: 'swiper_h6',
      classB: 'fsize26'
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    getData: function () {
      let that = this
      this.$axios.get(this.api + '/ClassTree' + this.actionExt, {
      }).then((res) => {
        // console.log('结果', res)
        if (res.data.code === 0) {
          that.list = res.data.list
          /* eslint-disable no-new */
          new Swiper('.swiper-container', {
            autoplay: false,
            observer: true, // 修改swiper自己或子元素时，自动初始化swiper
            observeParents: true, // 修改swiper自己或子元素时，自动初始化swiper
            speed: 1000,
            initialSlide: parseInt(that.list.length / 2),
            spaceBetween: 100,
            centeredSlides: true,
            slidesPerView: 6.17,
            pagination: '.swiper-pagination',
            paginationClickable: true
          })
        } else {
          this.$message(res.data.errMsg)
          this.$router.push({
            name: 'login'
          })
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    getTablelist: function (item) {
      this.$axios.post(this.api + '/SelectClass' + this.actionExt, {
        classId: item.CLASSID
      }).then((res) => {
        console.log('结果', res)
        if (res.data.code === 0) {
          this.$router.push({
            name: 'tablelist',
            params: {
              classType: item.CLASSTYPE,
              title: item.DESCRIPTION
            }
          })
          sessionStorage.setItem('classType', item.CLASSTYPE)
          sessionStorage.setItem('title', item.DESCRIPTION)
          sessionStorage.setItem('classId', item.CLASSID)
        }
      }).catch(function (error) {
        if (error.response) {
          var status = error.response.status.toString()
          console.log('转转类型之后', typeof (status))
          if (status === '50') {
            this.$message.error('服务器在尝试处理请求时发生内部错误')
          } else if (status.substring(0, 1) === '4') {
            this.$message.error('请求出错，可能是服务器未找到页面')
          } else {
            this.$message.error('未知错误')
          }
        }
      })
      // this.$router.push({
      //   name: 'tablelist',
      //   params: {
      //     classId: item.CLASSID,
      //     classType: item.CLASSTYPE
      //   }
      // })
      // sessionStorage.setItem('classId', item.CLASSID)
      // sessionStorage.setItem('classType', item.CLASSTYPE)
    }
  }
}
</script>
