    width: 100%;
    height: 100%;
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20viewBox%3D'0%200%20120%20120'%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20xmlns%3Axlink%3D'http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink'%3E%3Cdefs%3E%3Cline%20id%3D'l'%20x1%3D'60'%20x2%3D'60'%20y1%3D'7'%20y2%3D'27'%20stroke%3D'%236c6c6c'%20stroke-width%3D'11'%20stroke-linecap%3D'round'%2F%3E%3C%2Fdefs%3E%3Cg%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(30%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(60%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(90%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(120%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(150%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.37'%20transform%3D'rotate(180%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.46'%20transform%3D'rotate(210%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.56'%20transform%3D'rotate(240%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.66'%20transform%3D'rotate(270%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.75'%20transform%3D'rotate(300%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.85'%20transform%3D'rotate(330%2060%2C60)'%2F%3E%3C%2Fg%3E%3C%2Fsvg%3E");
    background-position: 50%;
    -webkit-background-size: 100%;
    background-size: 100%;
    background-repeat: no-repeat
}

.swiper-lazy-preloader-white:after {
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20viewBox%3D'0%200%20120%20120'%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20xmlns%3Axlink%3D'http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink'%3E%3Cdefs%3E%3Cline%20id%3D'l'%20x1%3D'60'%20x2%3D'60'%20y1%3D'7'%20y2%3D'27'%20stroke%3D'%23fff'%20stroke-width%3D'11'%20stroke-linecap%3D'round'%2F%3E%3C%2Fdefs%3E%3Cg%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(30%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(60%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(90%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(120%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(150%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.37'%20transform%3D'rotate(180%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.46'%20transform%3D'rotate(210%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.56'%20transform%3D'rotate(240%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.66'%20transform%3D'rotate(270%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.75'%20transform%3D'rotate(300%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.85'%20transform%3D'rotate(330%2060%2C60)'%2F%3E%3C%2Fg%3E%3C%2Fsvg%3E")
}

body {
  /* background: #eee; */
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}
body, html{
  width: 100%;
  height: 100%;
}

.layer-mask {
  width: 100%;
  height: 100%;
  opacity: 0.7;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
}

.swiper-slide-active .layer-mask {
  display: none;
}

.pc-banner {
  width: 100%;
  float: left;
}

@media screen and (max-width: 668px) {
  .pc-banner {
    background-size: auto 100%;
  }
}

.swiper-container {
  width: 100%;
  margin: 35px 0;
}

@media screen and (max-width: 668px) {
  .swiper-container {
    margin: 20px 0 15px;
  }
}

/*.swiper-slide {
  margin-top: 70px;
  margin-bottom: 70px;
}*/
.swiper-slide {
  margin-top: 10%;
}

@media screen and (max-width: 668px) {
  .swiper-slide {
    -webkit-transform: scale(0.97);
    transform: scale(0.97);
  }
}

.swiper-slide-duplicate-active {
  -webkit-transform: scale(1.15);
  transform: scale(1.15);
  z-index: 1002;
}
/*.swiper-slide-active,.swiper-slide-duplicate-active {
  -webkit-transform: scale(1.15);
  transform: scale(1.15);
  z-index: 1002;
}*/

@media screen and (max-width: 668px) {
  .swiper-slide-active,.swiper-slide-duplicate-active {
    -webkit-transform: scale(0.97);
    transform: scale(0.97);
  }
}

.none-effect {
  -webkit-transition: none;
  -moz-transition: none;
  -ms-transition: none;
  -o-transition: none;
}

.swiper-slide a {
  background: #fff;
  display: block;
  border-radius: 2px;
  width: 100%;/*2018.09.14*/
}

@media screen and (min-width: 668px) {
  .swiper-slide a:after {
    position: absolute;
    top: 0px;
    left: 0;
    display: block;
    box-sizing: border-box;
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 2px;
    box-shadow: 0 2px 6px rgba(0,0,0,0.3);
    box-shadow: 0 4px 20px 0 rgba(0,0,0,0.3);
  }

  .swiper-slide-active a:before {
    position: absolute;
    top: 410px;
    left: 0;
    display: block;
    box-sizing: border-box;
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 2px;
    /* box-shadow: 0 2px 6px rgba(0,0,0,0.3); */
    /* box-shadow: 0 4px 20px 0 rgba(0,0,0,0.3); */
    z-index: 10002;
  }
}

.swiper-slide-active a:after {
  background: none;
}

@media screen and (max-width: 668px) {
  .swiper-slide a {
    padding: 5px;
    border-radius: 2px;
  }
}

.swiper-slide img {
  width: 100%;
  /*border-radius: 5px;*/
  display: block;
  height: 100%;
  border: none;
}

@media screen and (max-width: 668px) {
  .swiper-slide img {
    border-radius: 2px;
  }
}

.swiper-pagination {
  position: relative;
  margin-bottom: 30px;
}

.swiper-pagination-bullet {
  background: #dbdbdb;
  margin-left: 4px;
  margin-right: 4px;
  width: 13px;
  height: 13px;
  opacity: 1;
  margin-bottom: 4px;
}

/*.swiper-pagination-bullet-active {
  width: 13px;
  height: 13px;
  background: #49a1e1;
  margin-bottom: 0;
}*/
.swiper-pagination-bullet-active {
  width: 13px;
  height: 13px;
  background: #909090;
  margin-bottom: 0;
}

@media screen and (max-width: 668px) {
  .swiper-pagination {
    position: relative;
    margin-bottom: 20px;
  }

  .swiper-pagination-bullet {
    background: #00a0e9;
    margin-left: 2px;
    margin-right: 2px;
    width: 8px;
    height: 8px;
    margin-bottom: 2px;
  }

  .swiper-pagination-bullet-active {
    width: 6px;
    height: 6px;
    background: #FFF;
    border: 3px solid #00a0e9;
    margin-bottom: 0;
  }
}

.button {
  width: 100%;
  margin: 0 auto;
  top: -290px;
  position: relative;
  z-index: 1002;
}

@media screen and (max-width: 1105px) {
  .button {
    width: 100%;
    top: -240px;
    z-index: 1002;
    position: relative;
  }
}

@media screen and (max-width: 800px) {
  .button {
    width: 100%;
    top: -200px;
    z-index: 1002;
    position: relative;
  }
}

.button div:hover {
  background-color: #333;
  opacity: 0.8;
  cursor: pointer;
}

.swiper-button-prev {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20viewBox%3D'0%200%2027%2044'%3E%3Cpath%20d%3D'M0%2C22L22%2C0l4.2%2C4.2L8.4%2C22l17.8%2C17.8L22%2C44L0%2C22z'%20fill%3D'%23ffffff'%2F%3E%3C%2Fsvg%3E") #000000 center 50%/50% 50% no-repeat;
  opacity: 0.5;
  background-size: 15px;
  cursor: pointer;
}

.swiper-button-next {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20viewBox%3D'0%200%2027%2044'%3E%3Cpath%20d%3D'M27%2C22L5%2C44l-4.2-4.2L18.6%2C22L0.8%2C4.2L5%2C0z'%20fill%3D'%23ffffff'%2F%3E%3C%2Fsvg%3E") #000000 center 50%/50% 50% no-repeat;
  opacity: 0.5;
  background-size: 15px;
  cursor: pointer;
}
@media screen and (max-width: 668px) {
  .button div {
    width: 28px;
    height: 28px;
  }
}
/*2018.09.14*/
.mainpage_header {
  box-shadow: 0px 5px 8px #b1d1f8;
  background-color: #2293FB !important;
  height: 120px;
}
.header_border {
  width: 100%;
  height: 100%;
  position: relative;
}
.header_logo {
  display: block;
  position: absolute;
  height: 100%;
  /* left: 80px;
  top: 24%; */
}
.header_logo img {
  width: auto;
  height: inherit;
}
.header_title {
  position: absolute;
  left: 44% !important;
  top: 36% !important;
}
.header_icon {
  color: #fff;
  position: absolute;
  right: 80px;
  top: 33%;
}
.header_icon span {
  display: inline-block;
}
.message {
  color: #fff !important;
  font-size: 25px !important;
}
.person {
  color: #fff !important;
  font-size: 25px !important;
}
.exit {
  color: #fff !important;
  font-size: 25px !important;
}
.swiper_next {
  position: relative;
  box-shadow: #c9c9c9 0px 0px 30px;
  cursor: pointer;
}
.swiper_next:hover {
  transform: scale(1.15);
  -webkit-transform: scale(1.15);
  z-index: 1002;
}
.swiper_next:hover .swiper_enter {
  display: block;
}
.swiper_img {
  width: 100%;
  height: 100%;
}
.swiper_h6, .fsize26 {
  font-family: mFont;
  position: absolute;
  top: 0px;
  width: 90%;
  height: auto;
  margin: 5px;
  font-size: 16px;
  color: #fff;
  text-align: left;
  vertical-align: top;
  z-index: 5;
}
/* .fsize26 {
  font-family: mFont;
  position: absolute;
  top: -35px;
  width: 90%;
  height: auto;
  font-size: 16px !important;
  left: 5px;
  text-align: left;
  color: #fff;
  z-index: 5;
} */
/* .fsize40{
  font-size: 40px!important;
} */
/*正常遮罩*/
.shade {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #000;
  top: 0;
  background: rgba(0,0,0,0.2);
  box-shadow: #b2b2b2 0px 0px 30px;
  z-index: 3;
}
.swiper_text {
  font-family: mFont;
  position: absolute;
  top: -42px;
  right: 3px;
  font-size: 15px;
  color: #7c7c7c;
  font-weight: bold;
}
.swiper_enter {
  position: absolute;
  /*left:-11%;*/
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #3a3d44;
  background: rgba(58,61,68,0.5);
  box-shadow: #b2b2b2 0px 0px 30px;
  font-family: '黑体';
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  display: none;
  z-index: 4;
}
.swiper_enter img{
  width: 60px;
  height: 32px;
  margin:130% auto 0px auto;
}

/*底部*/
.mainpage_footer{
  text-align: center;
  color: #fff;
  background: #2293FB;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
}
.mainpage_footer p {
  font-size:12px;
  font-family: '黑体';
  margin-top: 1.8%;
}

</style>



